.startBox {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1;
  background-color: #719545;
}

.container {
  height: fit-content;
  width: fit-content;
  position: relative;
  border-radius: 8px / 10px 10px 35px 35px;
  border: 1px solid #000a;
  border-top: 2px solid #000a;
  box-shadow: 0 0 8px #000 inset, -4px 0 2px #fff8, 4px 0 2px #2228;
}

.snake {
  font-size: 40px;
  margin: 20px;
  font-family: monospace;
  margin-bottom: 0;
  display: flex;
  color: #2c3f28;
}
.spin {
  width: 15px;
  height: 15px;
  background-color: #2c3f28;
  border-radius: 3px;
  margin-top: 27px;
  margin-left: 15px;
  margin-right: 15px;
  animation-name: spin;
  animation-duration: 2500ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.spinBox {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
}
.levels {
  display: flex;
  flex-direction: row;
  font-size: 36px;
}
.level {
  margin: 25px;
  font-size: 36px;
  background: fixed;
  font-family: monospace;
  border: dashed;
  border-radius: 5px;
  color: #2c3f28;
}

.row {
  display: flex;
  flex-direction: row;
  width: fit-content;
}

.cell {
  height: 10px;
  width: 10px;
}
.snakeBody {
  background-color: #2c3f28;
  border: 1px;
  border-radius: 3px;
}
.emptyCell {
  background-color: #9bc963;
}
.gameBox {
  align-items: center;
  display: flex;
  flex-direction: column;
  border: solid;
  border-width: 3px;
  border-radius: 64px;
  padding: 28px;
  border-color: #2c3f28;
  background-image: linear-gradient(to right, #0005 15%, transparent),
    linear-gradient(111deg, #2f2b31, #7d8299);
  box-shadow: 0 0 8px 4px #0006, 0 0 8px 4px #0005 inset;
  border-radius: 16% 16% 14% 14% / 31% 31% 54% 54%;
  background: #3f3d46;
}
.score {
  font-size: 20px;
  font-family: monospace;
  color: rgb(146 157 146);
}
.gameOverContainer {
  position: absolute;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.gameOverBox {
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 0.8;
  color: #9bc963;
  background-color: #3f3d46;
  padding: 12px;
}
.gameOver {
  font-size: 20px;
  font-family: monospace;
}
.restart {
  font-size: 19px;
  background: fixed;
  color: #9bc963;
  border-width: 2px;
  font-family: monospace;
  border-color: #9bc963;
  border: dashed;
  border-radius: 5px;
  padding: 10px;
}

/* button layout */
.directional-buttons {
  /* Define the size of the grid */
  /* Other things rely on this! Change at your own peril! */
  width: 14rem;
  height: 14rem;
  /* display the buttons in a grid */
  display: grid;
  /* leave a little gap between columns/rows */
  grid-gap: 1.5rem;
  /* define a 3 x 3 grid with middle column/row larger */
  grid-template-columns: 45fr 60fr 45fr;
  grid-template-rows: 45fr 60fr 45fr;
  /* name the cells in the grid */
  grid-template-areas:
    "....  up  ....."
    "left  ..  right"
    ".... down .....";
}
.direction {
  align-items: center;
}

/* base button */
.direction-button {
  /* the button colours */
  color: rgb(146 157 146);
  /* since there's no text, we can use the color value for the background */
  background: currentcolor;
  /* clears default button styles */
  border: none;
  /* makes sure the triangles are moved relative to the button */
  position: relative;
}

/* the overlapping outside triangle 
   * actually, it's just a rotated square
   */
.direction-button:before {
  /* make the triangle display */
  display: block;
  content: "";
  /* make the triangle able to be moved around relative to the button */
  position: absolute;
  /* approximately makes the diagonal the right size */
  width: 4.25rem;
  height: 4.25rem;
  /* rotate the square (to look like a triangle) */
  transform: rotate(45deg);
  /* use the current color value for the background to match the button */
  background: currentcolor;
}

/* the inner direction triangle */
.direction-button:after {
  /* make the triangle display */
  display: block;
  content: "";
  /* make the triangle able to be moved around relative to the button */
  position: absolute;
  /* same border-based triangle trick as before */
  border: 2rem solid transparent;
  /* using color value for border-color to make it easier to change */
  color: #3f3d46;
}

/* when the button is pressed */
.direction-button:active:after {
  /* make the inner triangle a different color! */
  color: rgb(255, 152, 152);
}

/* individual button styles */
.up {
  /* puts the button in the named grid cell */
  grid-area: up;
  /* only curves the outer corners */
  border-radius: 0.5rem 0.5rem 0 0;
}
.up:before {
  /* center the overlapping triangle horizontally */
  left: calc(50% - 2.125rem);
  /* position it so it overlaps just right */
  bottom: -2.125rem;
}
.up:after {
  /* do the border-based triangle trick */
  border-bottom-color: currentcolor;
  /* center inner triangle horizontally */
  left: calc(50% - 2rem);
  /* position it just right */
  top: -1rem;
}

.left {
  /* puts the button in the named grid cell */
  grid-area: left;
  /* only curves the outer corners */
  border-radius: 0.5rem 0 0 0.5rem;
}
.left:before {
  /* position it so it overlaps just right */
  right: -2.125rem;
  /* center the overlapping triangle vertically */
  top: calc(50% - 2.125rem);
}
.left:after {
  /* do the border-based triangle trick */
  border-right-color: currentcolor;
  /* center inner triangle vertically */
  top: calc(50% - 2rem);
  /* position it just right */
  left: -1rem;
}

.right {
  /* puts the button in the named grid cell */
  grid-area: right;
  /* only curves the outer corners */
  border-radius: 0 0.5rem 0.5rem 0;
}
.right:before {
  /* position it so it overlaps just right */
  left: -2.125rem;
  /* center the overlapping triangle vertically */
  top: calc(50% - 2.125rem);
}
.right:after {
  /* do the border-based triangle trick */
  border-left-color: currentcolor;
  /* center inner triangle vertically */
  top: calc(50% - 2rem);
  /* position it just right */
  right: -1rem;
}

.down {
  /* puts the button in the named grid cell */
  grid-area: down;
  /* only curves the outer corners */
  border-radius: 0 0 0.5rem 0.5rem;
}
.down:before {
  /* center the overlapping triangle horizontally */
  left: calc(50% - 2.125rem);
  /* position it so it overlaps just right */
  top: -2.125rem;
}
.down:after {
  /* do the border-based triangle trick */
  border-top-color: currentcolor;
  /* center inner triangle horizontally */
  left: calc(50% - 2rem);
  /* position it just right */
  bottom: -1rem;
}
